import { OverlayTrigger } from 'react-bootstrap';
import { Link } from "react-router-dom";
import styled from 'styled-components';
import { StyledTooltip }  from '../../shared/ReusedStyled';

// Individual buttons that appear on the homepage with the icon passed in. Used to navigate to the other pages on the site.
// Please note that this component follows the code example provided by bootstrap to set up the overlay trigger (https://react-bootstrap.netlify.app/components/overlays/#overlaytrigger)
const NavButton = (props: {img: string, className: string, href: string}) => {
    return (
        <OverlayTrigger placement="bottom" delay={{ show: 50, hide: 400 }} overlay={<ReStyledTooltip id="tooltip">{props.className}</ReStyledTooltip>}>
            <Link to={props.href} style={{ textDecoration: 'none' }}><StyledNavButton src={props.img} className={props.className} alt={props.className}/></Link>
        </OverlayTrigger>
    );
}

export default NavButton;

const StyledNavButton= styled.img`
    height: 16vmin;
    padding-right: 8px;
    pointer-events: none;
`;

const ReStyledTooltip = styled(StyledTooltip)`
    font-size: 16px;
`;